<template>
  <div class="container">
    <el-row type="flex" justify="center">
      <span class="title">请确认订单信息</span>
    </el-row>
    <el-row type="flex" justify="center">
      <el-form ref="form" :model="form" label-width="120px">
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :span="8">
            <el-form-item label-width="168px" :disabled="true" label="入住人">
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="入住房型">
              <el-input v-model="form.room_type"> </el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="入住日期">
              <el-input v-model="form.date1"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label-width="168px" :disabled="true" label="联系电话">
              <el-input v-model="form.phone"></el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="入住房号">
              <el-input v-model="form.room_no"> </el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="离店日期">
              <el-input v-model="form.date2"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg" justify="center">
          <span>已支付房费：{{ form.paid }}，已支付押金：{{ form.deposit }}</span>
        </el-row>
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :span="16">
          </el-col>
          <el-col :span="8">
            <router-link :to="{name: 'password'}">
              <el-button type="primary" class="btn_large">返回</el-button>
            </router-link>
            <router-link :to="{name: 'number_of_people'}">
              <el-button type="primary" class="btn_large">下一步</el-button>
            </router-link>
          </el-col>
        </el-row>
      </el-form>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '某某人',
        phone: '12345678',
        room_type: '大床房',
        room_no: '203',
        date1: '2018-01-12',
        date2: '2018-01-13',
        paid: 200,
        deposit: 100
      }
    }
  },
  methods: {
    onSubmit() {
      this.$message('submit!')
    },
    onCancel() {
      this.$message({
        message: 'cancel!',
        type: 'warning'
      })
    }
  }
}
</script>

<style scoped>
.title {
  font-size: 32px;
}

.el-row {
  margin-top: 20px;
}

.el-button {
  margin-left: 20px;
}
</style>
<style>
.el-input {
  color: white;
  font-size: 32px;
}

.el-form-item__label {
  width: 240px;
  color: white;
  font-size: 32px;
}
</style>



